@import "../colors.scss";

.container {
  background-color: $base;
  border: 1px solid $text;
  color: $text;
  border-radius: 15px;
}

.container {
  form {
    display: flex;
    flex-direction: column;
    padding: 20px;
    .header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      img {
        font-size: 30px;
        cursor: pointer;
        padding: 15px;
        color: $title;
      }
      svg {
        font-size: 30px;
        cursor: pointer;
        padding: 15px;
        color: $title;
      }
    }
    h2 {
      padding: 0 15px;
      color: $title;
    }
    label {
      padding: 15px;
      display: flex;
      align-items: center;
      justify-content: space-between;

      input,
      textarea {
        height: 30px;
        border: 1px solid $text;
        border-radius: 5px;
        background-color: $base;
        width: 75%;
        padding: 0 5px;
        color: $text;
        &::placeholder {
          color: rgba($text, 0.6);
        }
      }
      textarea {
        height: 60px;
        padding: 5px;
      }
    }
  }
}

.error {
  color: $title;
}
