@import "../../colors.scss";

.item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 45px;
  border: 1px solid $text;
  height: 70px;
  padding: 0 25px;
  margin: 15px 0;
  p {
    display: flex;
    align-items: center;
    strong,
    em,
    small {
      padding: 0 8px;
    }
  }
  .arrow svg {
    color: $title;
  }
}

.tags {
  padding: 0 10px;
  font-style: italic;
}
