@import "../../colors.scss";

.detail {
  border: 1px solid $text;
  color: $text;
  border-radius: 15px;
  padding: 20px 30px;

  .content {
    background-color: whitesmoke;
    margin: 30px 0 16px 0;
    padding: 10px;
    border-radius: 10px;
  }

  .author {
    display: flex;
    justify-content: space-between;
    align-items: center;
    span {
      display: flex;
      align-items: center;
      svg {
        padding: 5px;
      }
    }
  }

  .tagList {
    flex: 1;
    display: flex;
    p {
      margin: 0;
      padding: 4px;
      padding-right: 10px;
      color: $title;
      &:hover {
        border-radius: 10px;
        background: $title;
        color: $base;
      }
    }
  }

  .buttons {
    display: flex;
    margin: 16px 0;
    flex: 1;
    button {
      cursor: pointer;
      display: flex;
      justify-content: space-around;
      align-items: center;
      height: 35px;
      width: 120px;
      border-radius: 10px;
      font-size: 15px;
      border: 1px solid $text;
      background-color: $base;
      color: $text;
      margin-right: 10px;
    }
    .delete {
      background-color: $title;
      border: 1px solid $base;
      color: $base;
    }
    .edit {
      background-color: $text;
      border: 1px solid $base;
      color: $base;
    }
  }
}
