@import "../../colors.scss";

header {
  height: 60px;
  padding: 0 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: $base;
  color: $text;
  box-shadow: 0 2px 5px 0 grey;
  div {
    display: flex;
  }
  nav {
    display: flex;
    ul {
      display: flex;
      align-items: center;
      margin: 0;
      list-style: none;
      li {
        padding: 0 8px;
      }
      button.add {
        border: none;
        border-radius: 10px;
        background: $text;
        color: $base;
        font-size: 15px;
        height: 35px;
        align-self: center;
      }
    }
  }
}

.search {
  position: relative;
  display: flex;
  align-items: center;
  padding: 0 20px;
  input {
    width: 200px;
    padding: 10px;
    border: 1px solid $text;
    border-radius: 10px;
    background-color: inherit;
    height: 10px;
    color: $text;
    &::placeholder {
      color: $text;
      opacity: 1;
    }
  }
  svg {
    position: absolute;
    cursor: pointer;
    right: 30px;
  }
}
